.footer {
    background: #000;
    height: 7.5vh;
    width: 100vw;
    position: fixed;
    bottom: 0;
    z-index: 99;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 3.3vw;
    position: absolute;
    bottom: 0;
    .plus18 {
        position: absolute;
        right: 16.5vw;
        height: 2.8vh;
        
    }
    
    .money {
        width: 33vw;
        a {
            margin-right: 1.25vw;
            img {
                
                height: 2.3vh;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
    .app {
        width: 33vw;
        display: flex;
        justify-content: center;
        align-items: center;
      
        a {
            margin-right: 0.7vw;
            width: auto;
            img {
                height: 3.7vh;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
    .links {
        width: 33vw;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        a {
            height: auto;
            margin-right: 1vw;
            img {
                height: 1.6vh;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
}