.main-block {
    display: flex;
    flex-direction: column;
    align-items: center;
   
    .text {
        margin: 0 auto;
        
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 1;
        transform: translate(-11vw,-2vh);
        &__top {
            font-size: 1.64vw;
            line-height: 4.405555555555556vh;
            position: relative;
            font-family: Parimatch;
            font-weight: 700;
            color: #fff;
            letter-spacing: 20;
            text-transform: uppercase;
            
            width: 18.5546875vw;
            text-align: center;

           
                &:after {
                    content: '';
                    display: block;
                    -webkit-transform: skew(-16deg);
                    -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                    background: #7f8300;
                    height: 4.405555555555556vh;
                    position: absolute;
                    z-index: -1;
                    top: 0;
                    left: 0;
                    width: 18.5546875vw;
                }
        }
        &__h1 {
            font-size: 8.2421875vw;
            line-height: 6.875vw;
            font-family: Parimatch;
            font-weight: 700;
            text-transform: uppercase;
            text-align: end;
            margin-top: 2.78vh;
            span {
                display: inline-block;
                &:first-child {
                    margin-right: 7vw;
                }
                &:last-child {
                   
                }
            }
        }
        &__bottom {
            font-size: 3.984375vw;
            line-height: 3.05vw;
            font-family: Parimatch;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 1.777777777777778vh;
            color: rgba(0,0,0, 0.6);
        }
        &__btn {
            font-size: 1.875vw;
            text-transform: uppercase;
            line-height: 9.722222222222222vh;
            width: 25.390625vw;
            color: #f8ff13;
            font-family: Parimatch;
            font-weight: 700;
            position: relative;
            text-align: center;
            margin-top: 4.166666666666667vh;
            cursor: pointer;
            &-bg {
                height: 100%;
                width: 100%;
                -webkit-transform: skew(-16deg);
                -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                background: #110c0a;
                position: absolute;
                top: 0;
                left: 0;
                z-index: -1;
                &:after {
                    position: absolute;
                    content: '';
                    display: block;
                    background: #fff;
                    width: 0.078125vw;
                    height: 0.78125vw;
                    top:0.6640625vw;
                    right: 1.3671875vw;
                    -webkit-transform: skew(16deg);
                    -moz-transform: skew(16deg);
                    -o-transform: skew(16deg);
                }
                &:before {
                    position: absolute;
                    content: '';
                    display: block;
                    background: #fff;
                    width: 0.78125vw;
                    height: 0.078125vw;
                    top:0.6640625vw;
                    right: 1.3671875vw;
                    -webkit-transform: skew(16deg) translate(0.25vw, 0.3515625vw);
                    -moz-transform: skew(16deg) translate(0.25vw, 0.3515625vw);
                    -o-transform: skew(16deg) translate(0.25vw, 0.3515625vw);
                }

                
            }
            transition: all 0.5s ease;
            &:after {
                content: '';
                display: block;
                height: 100%;
                width: 100%;
                
                background: #110c0a;
                position: absolute;
                top: 0;
                left: 0;
                z-index: -2;
                opacity: 0.5;
                transition: all 0.5s ease;
                -webkit-transform: skew(-16deg) translate(-0.3515vw, -0.3515vw);
                -moz-transform: skew(-16deg) translate(-0.3515vw, -0.3515vw);
                    -o-transform: skew(-16deg) translate(-0.3515vw, -0.3515vw);
            }
            &:before {
                content: '';
                display: block;
                height: 100%;
                width: 100%;
                
                -webkit-transform: skew(-16deg) translate(0.3515vw, 0.3515vw);
                -moz-transform: skew(-16deg) translate(0.3515vw, 0.3515vw);
                    -o-transform: skew(-16deg) translate(0.3515vw, 0.3515vw);
                background: #fff;
                position: absolute;
                top: 0;
                left: 0;
                z-index: -5;
                transition: all 0.5s ease;
            }
            &:hover {
                filter: brightness(1.3);
                &:before {
                    -webkit-transform: skew(-16deg);
                -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                }
                &:after {
                    -webkit-transform: skew(-16deg);
                -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                }
            }
        }
    }
    .steps {
        display: flex;
        position: absolute;
        bottom: 15vh;
      
        &__item {
            display: flex;
            align-items: center;
            margin-right: 5.5vw;
            &:last-child {
                margin-right: 0;
            }
            &-digit {
                width: 2.86vw;
                height: 2.86vw;
                position: relative;
                
                margin-right: 0.78125vw;
                font-size: 1.640625vw;
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: Parimatch;
                font-weight: 400;
                &:before {
                    content: '';
                    display: block;
                    -webkit-transform: skew(-16deg);
                    -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                    background: #fff;
                    width: 2.86vw;
                    height: 2.86vw;
                    position: absolute;
                    z-index: -1;
                }
            }
            &.steps__item-1 {
                &:after {
                    content: '';
                    display: block;
                    -webkit-transform: skew(-16deg);
                    -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                    background: #919505;
                    width: 0.6640625vw;
                    height: 0.6640625vw;
                    position: absolute;
                    z-index: 1;
                    left:1.33125vw;
                    bottom: -0.33vw;
                    position: absolute;
                }
                .steps__item-digit {
                    &:after {
                        content: '';
                        display: block;
                        -webkit-transform: skew(-16deg);
                        -moz-transform: skew(-16deg);
                        -o-transform: skew(-16deg);
                        background: #000;
                        width: 2.1484375vw;
                        height: 2.1484375vw;
                        position: absolute;
                        z-index: -2;
                        left: 2px;
                        top: -4px;
                        position: absolute;
                    }
                }
            }
            &.steps__item-2 {
                position: relative;
                &:after {
                    content: '';
                    display: block;
                    -webkit-transform: skew(-16deg);
                    -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                    background: #000;
                    width: 0.6640625vw;
                    height: 0.6640625vw;
                    position: absolute;
                    z-index: 2;
                    left: -0.5vw;
                    bottom: 0.33vw;
                    position: absolute;
                }
                
                .steps__item-digit {
                    &:after {
                        content: '';
                        display: block;
                        -webkit-transform: skew(-16deg);
                        -moz-transform: skew(-16deg);
                        -o-transform: skew(-16deg);
                        background: #919505;
                        width: 1.171875vw;
                        height: 1.171875vw;
                        position: absolute;
                        z-index: -2;
                        right: -6px;
                        top: -3px;
                        position: absolute;
                    }
                }
                .steps__item-text {
                    &:after {
                        position: absolute;
                        content: '';
                        display: block;
                        background: rgba(0,0,0, 0.6);
                        width: 0.06vw;
                        height: 0.78125vw;
                        bottom:0vw;
                        left: 2.3671875vw;
                        // -webkit-transform: translate(0.25vw, 0.3515625vw);
                        // -moz-transform: translate(0.25vw, 0.3515625vw);
                        transform:  translate(0.3vw, 0.31vw);
                    }
                    &:before {
                        position: absolute;
                        content: '';
                        display: block;
                        background: rgba(0,0,0, 0.6);
                        width: 0.78125vw;
                        height: 0.06vw;
                        bottom:0vw;
                        left: 2.3671875vw;
                        
                    }
                }
            }

            &.steps__item-3 {
                position: relative;
                &:after {
                    content: '';
                    display: block;
                    -webkit-transform: skew(-16deg);
                    -moz-transform: skew(-16deg);
                    -o-transform: skew(-16deg);
                    background: #919505;
                    width: 0.6640625vw;
                    height: 0.6640625vw;
                    position: absolute;
                    z-index: 2;
                    left: -0.5vw;
                    bottom: 0.33vw;
                    position: absolute;
                }
                
                .steps__item-digit {
                    &:after {
                        content: '';
                        display: block;
                        -webkit-transform: skew(-16deg);
                        -moz-transform: skew(-16deg);
                        -o-transform: skew(-16deg);
                        background: #000;
                        width: 2.1484375vw;
                        height: 2.1484375vw;
                        position: absolute;
                        z-index: -2;
                        left: -0.12vw;
                        bottom: -0.15625vw;
                        position: absolute;
                    }
                }
                
            }
            &-text {
                text-transform: uppercase;
                font-family: Parimatch;
                font-weight: 300;
                font-size: 1.26vw;
                
            }
           
        }
    }


    .dop-text {
        position: absolute;
        bottom: 11vh;
        color: rgba(127,131,0, 0.5);
        font-size: 0.78vw;
        font-family: Parimatch;
        font-weight: 300;
        @media screen and (orientation: landscape) {
            right: 3.3vw;
        }
    }
    .portrait-img {
        position: absolute;
        width: 100vw;
        height: 100vh;
        top:0;
        left: 0;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;

        .plus-top {
            position: absolute;
            top: 0.5vh;
            left: calc(50% - 11vw);
            width: 24vw;
        }
        .plus-left {
            position: absolute;
            bottom: 10.5vh;
            left: -4.5vw;
            z-index: -4;
            width: 26vw;
        }
        .plus-right {
            position: absolute;
            top: 0.5vh;
            right: 4.5vw;
            z-index: -4;
           
           
            width: 18vw;
        }
    }
    .man {
        position: absolute;
        @media screen and (orientation: landscape) {
            bottom: 0vh;
            left: 0vw;
        }
        
        width: 42.3828125vw;
        z-index: -3;
        margin-left: -0.5vw;
        margin-bottom: 6vh;
         animation: 5s ease infinite movement;
        
    }
    .jugg {
        position: absolute;

        margin-top: -7vh;
        margin-left: 10.7vw;
        width: 25.78125vw;
        z-index: -2;
        animation: 5s ease infinite movement;
    }
    .girl {
        
        position: absolute;
        margin-right: -6.5vw;
        @media screen and (orientation: landscape) {
            top: 25vh;
            right: 0vw;
        }
        
        width: 31.6vw;
        z-index: -3;
        animation: 5s ease infinite movement;
    }
    @-webkit-keyframes movement {
        0%   {transform: translateY(-3vh);}
        50%  {transform: translateY(3vh);}
        100%    {transform: translateY(-3vh);}
    }

    
}




