  .glitch-wrapper{
  text-align:center;
  animation:glitch 5s linear infinite;
  user-select: none;
  pointer-events: none;
  }

  .glitch-text, button{
  font-family: monospace;
  font-weight:bold;
  }
  .glitch-text{
  position:relative;
  z-index:2;
  opacity:.9;
  margin:0 auto;
  text-shadow:0px 0px 3px #110c0a;
  animation:glitch 5s infinite;
  cursor:pointer;
  color:#110c0a;
  user-select: none;
  pointer-events: none;
  }
  .glitch-wrapper-2 {
    animation:glitch 6s infinite;
  }
  .glitch-text-3 {
    animation:glitch 4s infinite;
    color: #110c0a !important;
    text-shadow:0px 0px 3px #110c0a;
  }
  .glitch-wrapper_1 {
    animation:glitch 3s infinite;
  }
  .glitch-wrapper_2 {
    animation:glitch 5s infinite;
    }
    .glitch-wrapper_3 {
        animation:glitch 4.2s infinite;
    }
  .paused{
  animation-play-state: paused;
  }
  
  
  /* JUST RANDOM VALUES HERE! */
  @keyframes glitch{
   3%{text-shadow:7px 7px 0px #eff52f;}
  6%{text-shadow:-9px 4px 0px white;transform:translate(4px,-10px) skewX(124deg);}
  7%{text-shadow:3px -4px 0px #7f8300;}
  9%{text-shadow:0px 0px 3px white;transform:translate(0px,0px) skewX(0deg);}
  2% , 54%{transform:translateX(0px) skew(0deg);}
  55%{transform:translate(-2px,6px) skew(-153deg);}
  56%{text-shadow:0px 0px 3px white;transform:translate(0px,0px) skew(0deg);}
  57%{text-shadow:7px 7px 0px #eff52f;transform:translate(4px,-10px) skew(-50deg);}
  58%{text-shadow:0px 0px 3px white;transform:translate(0px,0px) skew(0deg);}
  62%{text-shadow:3px -4px 0px #7f8300;transform:translate(0px,20px) skew(0deg);}
  63%{text-shadow:0px 0px 3px white;transform:translate(4px,-2px) skew(0deg);}
  64%{transform:translate(1px,3px) skew(-130deg);}
  65%{transform:translate(-7px,2px) skew(60deg);}
  66%{transform:translate(0px,0px) skew(0deg);}

  }