

@font-face {
    font-family: Parimatch;
    src: url(../fonts/Parimatch-Bold.woff) format('woff'); 
    font-weight: 700;
}
@font-face {
    font-family: Parimatch; 
    src: url(../fonts/Parimatch-Light.woff) format('woff');
    font-weight: 300;
}
@font-face {
    font-family: Parimatch; 
    src: url(../fonts/ParimatchMedium.woff) format('woff'); 
    font-weight: 400;
}


* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    &:focus {
        outline: none;
    }
    
}
body {
    background: url(../img/bg.jpg) no-repeat center center/cover;
    position: relative;
    height: 100vh;
    width: 100vw;
    max-height: 100vh;
    max-width: 100vw;
    overflow: hidden;
}
button {
    border: none;
    background: none;
    outline: none;
    &:focus {
        outline: none;
    }
}
a {
    text-decoration: none;
    text-transform: none;
    outline: none;
  
    &:focus {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:active {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:hover {
        text-decoration: none;
        text-transform: none;
        outline: none;
      
    }
}


@import "header";
@import "main-block";
@import "footer";
@import "media";
